export enum MenuType {
  Item = 'item',
  Separator = 'separator',
}
export interface IUiMenuItem {
  name?: string;
  /**
   * 
   */
  tip?: string;
  /**
   * 
   */
  type?: MenuType;
  /**
   * 
   */
  enabled?: boolean;
  /**
   * 
   */
  visible?: boolean;
  /**
   * falseiconicon
   */
  selected?: boolean;
  /**
   * icon
   */
  icon?: string;
  /**
   * 
   */
  shortKey?: string;
  callback?: (item: IUiMenuItem, event: MouseEvent) => void | null;
  /**
   * 
   */
  enter?: ((item: IUiMenuItem) => void) | null;
  /**
   * 
   */
  leave?: ((item: IUiMenuItem) => void) | null;
  /**
   * 
   */
  items?: IUiMenuItem[];
}
export interface MenuListData {
  /**ID */
  id: string;
  menus: IUiMenuItem[];
}
export const Msg = {
  ShowMenu: 'show-menu',
  CleanMenu: 'clean-menu',
  ResetMenu: 'reset-menu',
  ResetSelect: 'reset-select',
  ResetSubMenuListID: 'reset-submenulist-id',
};

export interface MenuOptions {
  x?: number;
  y?: number;
  /**
   * [0-1]
   */
  opacity?: number;
  /** */
  clean?: boolean;
  cb?: (id: string) => void;
  /** */
  element?: HTMLElement;
}

export function showMenuByMouseEvent(event: MouseEvent, newMenus: IUiMenuItem[], options?: MenuOptions): void {
  options = options || {};
  options.x = event.clientX;
  options.y = Math.abs(event.clientY);
  ccui.Emitter.emit(Msg.ShowMenu, options, newMenus || []);
}
export const ProvideKeys = {
  SetSubMenuListID: 'SetSubMenuListID',
  Emitter: 'Emitter',
};
